<!--
 * @Description: 
 -->
 <template>
  <div id="contentcontainer">
    <!---->
    <div class="box1-left">
      <currentLocate></currentLocate>
      <div class="box2-left" v-if="showReport==1">
        <div id="report">
          <router-link to="report1"></router-link>
        </div>
      </div>
      <div class="box2-left">
        <keep-alive>
          <component :is="currentType" :queryProp="query"></component>
        </keep-alive>
      </div>
    </div>
    <!---->
    <div class="box4-right">
      <div class="box5-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs2[1]" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <div class="box6-right">
        <user-box></user-box>
      </div>
      <div class="box7-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs2[0]" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <div class="box12-right">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="title-active"></a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <slide2></slide2>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>
 <script>
import UserBox from "@/components/common/tools/user-box";
import slide2 from "@/components/common/tools/slide2";
import currentLocate from "@/components/common/tools/currentLocate";
import PbList from "@/components/common/tools/pb-list";
import mainList from "@/components/index/content/main-list";
import mainListTalent from "@/components/index/content/main-list-talent";
import mainListProduct from "@/components/index/content/main-list-product";
import mainListMeeting from "@/components/index/content/main-list-meeting";
import { proofreading } from "@/api/getData";

/**
 * @description:
 * @query "queryMsg":{"message":{"name":"","belong":"","path":"/nt","newsUrl":"/information/findInformationInfo"}}
 * @query2 "queryMsg":{"message":{belong: "","fa": "fa-commenting","name": """newsUrl": "/information/findInformationInfo",
 *                    "path": "/news_1","requestPath": "/information/findInformationList",type: 2}}
 */
export default {
  name: "contentContainer",
  data() {
    return {
      query: JSON.parse(this.$route.query.queryMsg), //JSON[Object,Object]
      showReport: 0
    };
  },
  computed: {
    tabs2() {
      let value = global.tabs;
      let value1, value2;
      value1 = value.filter(item => {
        if (item.belong == "") return true;
      });
      value2 = value.filter(item => {
        if (item.belong == "") return true;
      });
      if (!value[0].message && !value[0].message && !value[0].message) {
        proofreading.call(this); //
      }
      value = [value1, value2];
      return value;
    },
    currentType() {
      switch (this.query.message.name) {
        case "":
          return "main-list-talent";
          break;
        case "":
          return "main-list-talent";
          break;
        case "":
          return "main-list-talent";
          break;
        case "":
          return "main-list-product";
          break;
        case "":
          return "main-list-meeting";
          break;
        case "":
          return "main-list-meeting";
          break;
        default:
          return "main-list";
          break;
      }
    }
  },
  methods: {
    init() {
      if (JSON.parse(this.$route.query.queryMsg).message.name == "") {
        //
        this.showReport = 1;
      } else this.showReport = 0;
      //
      let localName = this.query.message.name;
      let localMenu = this.query.message.belong;
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == localMenu
      );
      let index2;
      global.currentPath.path = localName;
      if (index != -1) {
        index2 = this.$router.options.routes[0].children[
          index
        ].children.findIndex(menu => menu.showSecondName == localName);
        global.currentPath.data = [index, index2];
      } else {
        global.currentPath.data = [index];
      }
    }
  },
  watch: {
    $route() {
      if (this.$route.query.queryMsg) {
        this.query = JSON.parse(this.$route.query.queryMsg);
        this.init();
      }
    }
  },
  activated() {
    this.query = JSON.parse(this.$route.query.queryMsg);
    this.init();
  },
  components: {
    UserBox,
    slide2,
    PbList,
    mainList,
    mainListTalent,
    mainListProduct,
    currentLocate,
    mainListMeeting
  }
};
</script>
<style lang="scss" scoped>
#contentcontainer /deep/ img {
  max-width: 650px;
}
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin w-box {
  margin: 5px auto;
  width: 1200px;
  height: 312px;
  overflow: hidden;
  margin-bottom: 18px;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
#contentcontainer {
  @include adv;
  height: 100%;
}
/**/
@mixin litter_one_tab_box {
  @include i-box;
  @include border;
  height: 312px;
  width: 390px;
  float: right;
  margin-bottom: 10px;
}
.hot_search {
  @include adv;
  height: 100%;
  //margin-left:15px;
  padding: 13px;
  background: rgba(255, 255, 255, 0.95);
}
.box1-left {
  margin-top: 20px;
  width: 790px;
  float: left;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
  margin-bottom: 20px;
}
.box2-left {
  margin: 10px 0;
}

/**/
.box3-left {
  //@include i-box;
  height: 100%;
  margin: 10px 0;
  background: rgba(255, 255, 255, 0.95);
}
.box4-right {
  margin-top: 20px;
  width: 390px;
  //@include i-box;
  float: right;
}
.box4-right div {
  margin-right: 0.5px;
}
.box12-right,
.box5-right,
.box6-right,
.box7-right {
  @include litter_one_tab_box;
  background: rgba(255, 255, 255, 0.95);
}
.box8-left {
  //@include i-box;
  float: left;
  width: 795px;
  margin: 10px 0;
}
.box9-left {
  @include i-box;
  float: left;
  width: 795px;
  height: 250px !important;
  margin: 10px 0;
}
.box10-left {
  //@include i-box;
  float: left;
  width: 795px;
  height: 50px;
  margin: 10px 0;
}
.box11-left {
  //@include i-box;
  float: left;
  width: 795px;
  height: 400px;
  margin: 10px 0;
}
//
.image {
  margin: 0 auto;
  width: 200px;
  margin-left: 300px;
}
.Industry_Report_span_style {
  float: right;
  margin-right: 200px;
}
.Industry_Report_span0_style {
  float: right;
}
#Industry_Report_a {
  text-decoration: underline;
  color: #127de7;
}
#Industry_Report_a:hover {
  text-decoration: underline;
  color: red;
}
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}

#report {
  cursor: pointer;
  background: #f6f6f6;
  font-size: 17px;
  padding: 10px 20px;
  font-weight: 600;
  border: 1px solid #e6e6e6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #469;
}
#report:hover {
  color: #127de7;
}
</style>



