//router.index.js
import Vue from 'vue'
import Router from 'vue-router'
import contain from '@/components/index/contain'
import home from '@/components/index/tabs/home'
import News from '@/components/index/tabs/news'
import Policy from '@/components/index/tabs/policy'
import Association from '@/components/index/tabs/association'
import Enterprise from '@/components/index/tabs/enterprise'
import Product from '@/components/index/tabs/product'
import Tech from '@/components/index/tabs/tech'
import Talent from '@/components/index/tabs/talent'
import Exhibition from '@/components/index/tabs/exhibition'
import Meeting from '@/components/index/tabs/meeting'
import Public from '@/components/index/tabs/public'
import About from '@/components/index/tabs/about'
import Io from '@/components/index/tabs/io'
import Mall from '@/components/index/tabs/mall'
import search from '@/components/index/search'
import Content from '@/components/index/content/content'
import ContentList from '@/components/index/content/Content-list'


Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '',
      component: contain,
      children: [
        {
          path: '',
          redirect: home,
        },
        //
        {
          showName: "",
          path: "/home",
          icon: "fa-home",
          name: 'home',
          menuShow: true,
          component: home,
        },
        //
        {
          showName: "",
          path: "/news",
          icon: "fa-commenting",
          menuShow: true,
          component: News,
          children: [
            {
              path: '',
              name: 'news_0',
              component: () => import('@/components/index/tabs/news-default'),
            },//
            {
              path: '/news_1',
              name: 'news_1',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/news_2',
              name: 'news_2',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/news_3',
              name: 'news_3',
              component: ContentList,
              redirect: { name: 'policy' },
              showSecondName: ''
            },//
            {
              path: '/news_4',
              name: 'news_4',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/news_content',
              name: 'news_content',
              component: Content,
            },
          ]
          // alias:'/news',
        },
        //
        {
          showName: "",
          path: "/policy",
          icon: "fa-file-text",
          name: 'policy',
          menuShow: true,
          component: Policy,
          children: [
            {
              path: '/policy_content',
              name: 'policy_content',
              component: Content,
            },
          ]
          // alias:'/policy',
        },
        //
        {
          showName: "",
          path: "/association",
          icon: "fa-users",
          menuShow: true,
          component: Association,
          children: [
            {
              path: '',
              name: 'association_0',
              component: () => import('@/components/index/tabs/association-default'),
            },//
            {
              path: '/association_1',
              name: 'association_1',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/association_2',
              name: 'association_2',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/association_3',
              name: 'association_3',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/association_4',
              name: 'association_4',
              component: ContentList,
              showSecondName: ''
            },//
          ]
          // alias:'/association',
        },
        //
        {
          showName: "",
          path: "/enterprise",
          icon: "fa-globe",
          menuShow: true,
          component: Enterprise,
          children: [
            {
              path: '',
              name: 'enterprise_0',
              component: () => import('@/components/index/tabs/enterprise-default'),
            },//
            {
              path: '/enterprise_1',
              name: 'enterprise_1',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/enterprise_2',
              name: 'enterprise_2',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/enterprise_3',
              name: 'enterprise_3',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/enterprise_4',
              name: 'enterprise_4',
              component: ContentList,
              showSecondName: ''
            },//
          ]
          // alias:'/enterprise',
        },
        //
        {
          showName: "",
          path: "/product",
          icon: "fa-android",
          menuShow: true,
          component: Product,
          children: [
            {
              path: '',
              name: 'product_0',
              component: () => import('@/components/index/tabs/product-default'),
            },//
            {
              path: '/product_1',
              name: 'product_1',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/product_2',
              name: 'product_2',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/product_3',
              name: 'product_3',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/product_4',
              name: 'product_4',
              component: ContentList,
              showSecondName: ''
            },//
          ]
        },
        //
        {
          showName: "",
          path: "/tech",
          icon: "fa-lightbulb-o",
          menuShow: true,
          component: Tech,
          children: [
            {
              path: '',
              name: 'tech_0',
              component: () => import('@/components/index/tabs/tech-default'),
            },//
            {
              path: '/tech_1',
              name: 'tech_1',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/tech_2',
              name: 'tech_2',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/tech_3',
              name: 'tech_3',
              component: ContentList,
              showSecondName: ''
            },//
          ]
        },
        // //
        // {
        //   showName: "",
        //   path: "/io",
        //   icon: "fa-tachometer",
        //   name: 'io',
        //   menuShow: true,
        //   component: Io,
        // },
        // //
        // {
        //   showName: "",
        //   path: "/mall",
        //   icon: "fa-shopping-cart",
        //   name: 'mall',
        //   menuShow: true,
        //   component: Mall,
        // },
        //
        {
          showName: "",
          path: "/talent",
          icon: "fa-id-badge",
          menuShow: true,
          component: Talent,
          children: [
            {
              path: '',
              name: 'talent_0',
              component: () => import('@/components/index/tabs/talent-default'),
            },//
            {
              path: '/talent_1',
              name: 'talent_1',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/talent_2',
              name: 'talent_2',
              component: ContentList,
              showSecondName: ''
            },//
            {
              path: '/talent_3',
              name: 'talent_3',
              component: ContentList,
              showSecondName: ''
            },//
          ]
        },
        //
        {
          showName: "",
          path: "/exhibition",
          icon: "fa-tasks",
          menuShow: true,
          component: Exhibition,
          children: [
            {
              path: '',
              name: 'exhibition_0',
              component: () => import('@/components/index/tabs/exhibition-default')
            },//
            {
              path: '/exhibition_1',
              name: 'exhibition_1',
              component: ContentList,
              showSecondName: ''
            },//
          ]
        },
        //
        {
          showName: "",
          path: "/meeting",
          icon: "fa-pencil-square-o",
          menuShow: true,
          component: Meeting,
          children: [
            {
              path: '',
              name: 'meeeting_0',
              component: () => import('@/components/index/tabs/meeting-default')
            },//
            {
              path: '/meeting_1',
              name: 'meeting_1',
              component: ContentList,
              showSecondName: ''
            },//
          ]
        },
        //
        {
          showName: "",
          path: "/public",
          icon: "fa-weixin ",
          name: 'public',
          menuShow: true,
          component: Public,
        },
        //
        {
          showName: "",
          path: "/about",
          icon: "fa-address-book",
          name: 'about',
          menuShow: true,
          component: About,
        },
        //
        {
          path: '/content',
          name: 'content',
          component: Content,
        },
        //
        {
          showName: "",
          path: "/recruitment",
          icon: "fa-id-badge",
          component: () => import('@/components/index/tabs/recruitment'),
          children: [
            {
              path: '',
              name: 'recruitment_0',
              component: () => import('@/components/index/tabs/recruitment-default'),
              showSecondName: ''
            },//
            {
              path: '/recruitment_1',
              name: 'recruitment_1',
              component: () => import('@/components/index/content/recruitment-content'),
              showSecondName: ''
            },//
          ]
        },
        //
        {
          showName: "",
          path: '/searchPage',
          name: 'searchPage',
          component: () => import('@/components/index/tabs/searchPage'),
          children: [
            {
              path: '',
              name: 'searchPage_0',
              component: ContentList,
            },
          ]
        },
        //
        {
          showName: "",
          path: '/memberCenter',
          name: '',
          component: () => import('@/components/userCenter/member-center'),
          children: [
            //=
            {
              showSecondName: '',
              path: '',
              name: 'member_0',
              component: () => import('@/components/userCenter/member-default')
            },
            //
            {
              showSecondName: '',
              path: '/member_1',
              name: 'member_1',
              component: () => import('@/components/userCenter/register/member-register')
            },
            {
              showSecondName: '',
              path: '/member_2',
              name: 'member_2',
              component: () => import('@/components/userCenter/member-content')
            },
          ]
        },
        //
        {
          showName: "",
          path: '/userCenter',
          name: '',
          component: () => import('@/components/userCenter/personal-center'),
          children: [
            {
              showSecondName: '',
              path: '',
              name: 'user_0',
              component: () => import('@/components/userCenter/personal-default')
            },
            ////
            {
              showSecondName: '',
              path: '/user_1',
              name: 'user_1',
              component: () => import('@/components/userCenter/register/user-register')
            }
          ]
        },
        {
          path: '/report1',
          component: () => import('@/components/report/report1'),
        },
      ]
    },
    {
      path: '/',
      redirect: contain,
    },
  ]
})

export default router



