          belong: "", //
          message: null //
        },
        {
          name: "",
          belong: "", //
          message: null //
        }
      ],
      currentIndex: 0
    };
  },
  methods: {},
  watch: {
    currentIndex: {
      //
      handler(val) {
        //
      },
      immediate: true //watch
    }
  },
  mounted() {
    this.message[1].message = [
      {
        title: "",
        time: "2018-09-20"
      },
      {
        title: "AI",
        time: "2018-09-20"
      },
      {
        title: " ",
        time: "2018-09-20"
      },
      {
        title: "AI",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      },
      {
        title: " ",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      }
    ];
    this.message[2].message = [
      {
        title: "KR 1000 titan F",
        time: "2018-09-20"
      },
      {
        title: "KR 470-2 PA",
        time: "2018-09-20"
      },
      {
        title: "KR 3R540",
        time: "2018-09-20"
      },
      {
        title: "KR 600 R2830 F",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      }
    ];
  },
  components: {
    UserLogin
  }
};
</script>

<style lang="scss" scoped>
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}
.title li {
  float: left;
}
.widget-title a {
  cursor: pointer;
  color: #7b7b7b;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.widget-content-box > div:first-child{
    margin-top: -3px;
}
</style>



